/* Please ❤ this if you like it! */

*,
*::before,
*::after {
  box-sizing: border-box;
}

body{
  font-family: roboto;
  font-weight: 400;
  font-size: 15px;
  line-height: 1.7;
  color: #1f2029;
  background-color: #fff;
  overflow: hidden;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 101%;
  overflow-y: scroll;
}

input, select{
  outline: none;
}

input, textarea{
  padding-left: 9px;
  padding-right: 9px;
}

.section{
  position: relative;
  width: 100%;
  display: block;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

[type="checkbox"]:checked,
[type="checkbox"]:not(:checked){
  position: absolute;
  left: -9999px;
}

.modal-btn:checked + label,
.modal-btn:not(:checked) + label{
  position: relative;
  line-height: 2;
  height: 35px;
  transition: all 200ms linear;
  border-radius: 4px;
  width: 240px;
  display: inline-flex;
  -moz-align-items: center;
  -ms-align-items: center;
  align-items: center;
  -moz-justify-content: center;
  -ms-justify-content: center;
  justify-content: center;
  -ms-flex-pack: center;
  text-align: center;
  align-self: center;
  border: none;
  cursor: pointer;
  color: #ffffff;
  box-shadow: 0 12px 35px 0 rgba(16,39,112,.25);
}

.modal-btn:not(:checked) + label:hover{
  background-color: #000000;
}

.modal-btn:checked + label .uil,
.modal-btn:not(:checked) + label .uil{
  margin-left: 10px;
  font-size: 18px;
}

.modal-btn:checked + label:after,
.modal-btn:not(:checked) + label:after{
  position: fixed;
  top: 30px;
  right: 30px;
  z-index: 110;
  width: 40px;
  border-radius: 3px;
  height: 30px;
  text-align: center;
  line-height: 30px;
  font-size: 18px;
  background-color: #000000;
  color: #ff0707;
  content: 'x';
  box-shadow: 0 12px 25px 0 rgba(16,39,112,.25);
  transition: all 200ms linear;
  opacity: 0;
  pointer-events: none;
  -webkit-transform: translateY(20px);
  transform: translateY(20px);
}

.modal-btn:checked + label:hover:after,
.modal-btn:not(:checked) + label:hover:after{
  background-color: #ff0707;
  color: #ffeba7;
}

.modal-btn:checked + label:after{
  transition: opacity 300ms 300ms ease, background-color 250ms linear, color 250ms linear, -webkit-transform 300ms 300ms ease;
  transition: opacity 300ms 300ms ease, transform 300ms 300ms ease, background-color 250ms linear, color 250ms linear;
  transition: opacity 300ms 300ms ease, transform 300ms 300ms ease, background-color 250ms linear, color 250ms linear, -webkit-transform 300ms 300ms ease;
  opacity: 1;
  pointer-events: auto;
  -webkit-transform: translateY(0);
  transform: translateY(0);
}

.modal{
  position: fixed;
  display: block;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0 auto;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 100;
  overflow-x: hidden;
  background-color: rgba(31,32,41,.75);
  pointer-events: none;
  opacity: 0;
  transition: opacity 250ms 700ms ease;
}

.modal-btn:checked ~ .modal{
  pointer-events: auto;
  opacity: 1;
  transition: all 300ms ease-in-out;
}

.modal-wrap {
  position: relative;
  display: block;
  width: 100%;
  max-width: 400px;
  margin: 0 auto;
  margin-top: 20px;
  margin-bottom: 20px;
  border-radius: 4px;
  overflow: hidden;
  padding-bottom: 20px;
  background-color: #fff;
  align-self: center;
  box-shadow: 0 12px 25px 0 rgba(199,175,189,.25);
  opacity: 0;
  -webkit-transform: scale(0.6);
  transform: scale(0.6);
  transition: opacity 250ms 250ms ease, -webkit-transform 300ms 250ms ease;
  transition: opacity 250ms 250ms ease, transform 300ms 250ms ease;
  transition: opacity 250ms 250ms ease, transform 300ms 250ms ease, -webkit-transform 300ms 250ms ease;
}

.modal-wrap img {
  display: block;
  width: 100%;
  height: auto;
}

.modal-wrap p {
  padding: 20px 30px 0 30px;
}

.modal-btn:checked ~ .modal .modal-wrap{
  opacity: 1;
  -webkit-transform: scale(1);
  transform: scale(1);
  transition: opacity 250ms 500ms ease, -webkit-transform 350ms 500ms ease;
  transition: opacity 250ms 500ms ease, transform 350ms 500ms ease;
  transition: opacity 250ms 500ms ease, transform 350ms 500ms ease, -webkit-transform 350ms 500ms ease;
}

