.path {
  stroke-dasharray: 300;
  stroke-dashoffset: 300;
  -webkit-animation: dash 4s   alternate infinite;
  animation: dash 4s   alternate infinite;
}

@-webkit-keyframes dash{
  0%, 30%{
    fill: 4B4B62;
    stroke-dashoffset:   0;
  }

  80%,100%{
    fill: transparent;
    stroke-dashoffset:  -200;
  }
}

@keyframes dash{
  0%, 30%{
    fill: 4B4B62;
    stroke-dashoffset:   0;
  }

  80%,100%{
    fill: transparent;
    stroke-dashoffset:  -200;
  }
}
