@media screen and (max-width: 600px) {
  .form-container {
    max-width: 92%;
    
  }

  .newsletter-btn{
    font-size: .9rem;
    
  }
}

@media screen and (max-width: 480px){
  .newsletter-btn{
    background-color:red;
    color: white;
  }
}
@media screen and (max-width: 340px) {
  input[type="email"] {
    display: block;
    font-size: 0.8rem;
    min-width: 100%;
  }

  input[type="submit"] {
    display: block;
    min-width: 100%;
  }
}
