@media screen and (max-width: 1098px) {
  .content-container {
    flex-flow: column;
    align-items: center;
    margin: 0;
  }

  .post-heading-container {
    width: 100vw;
  }

  .post-heading-container .post-heading {
    min-width: 100%;
    padding: 0 20px;
    margin: 0;
  }

  .post-cover {
    margin: 0;
    padding: 0 20px;
    width: 100vw;
    height: 550px;
  }

  .post-cover img {
    min-width: 100%;
    min-height: 100%;
  }

  .content-container .post-body {
    min-width: 100%;
    padding: 0 20px;
  }

  .content-container .comment-form {
    max-width: 100%;
    margin: 0;
  }

  .comment-form form {
    max-width: 100%;
    margin: 0;
    padding: 0 20px;
  }

  .comments-section{
      padding-left: 20px;
      margin-left: 0;
  }

  form input {
    width: 100%;
  }

  .second-content {
    width: 100%;
    padding: 0 20px;
  }

  .caption-section {
    padding: 0 12px;
    margin: 0;
  }

  .second-content p {
    width: 100%;
    padding: 0;
    
  }

  .second-content h2{
      margin-left: 0;
  }
  .related-posts-container {
    max-width: 100%;
    margin-left: 0;
  }

  .recent-posts-container{
      margin-left: 0;
  }
  .socials{
      margin-left: 0;
  }
}
