.cards-container{

max-width: 1150px;
padding: 0 10px;
margin: 0 auto;

}

.swiper-container {
  width: 100%;
  
}


.cards-container::-webkit-scrollbar {
  display: none;
}

/* .card {
  flex-shrink: 0;
  margin: 0 40px;
  animation: animate 6s ease-in-out .3s;
  
} */

@media screen and (max-width: 660px) {
  .cards-container {
    max-width: 660px;
    display: grid;
    grid-template-columns: repeat(2, minmax(100%, 530px));
    grid-row-gap: 15px;
  }

 
}



