body {
  width: 100%;
  height: 100%;
}

@media screen and (max-width: 768px) {
  * {
    box-sizing: border-box;
  }
  .filter {
    display: none;
  }

  .filter-container {
    flex-flow: column;
  }

  .filter-books {
    order: 2;
  }

  .filter-mobile {
    max-width: 90%;
  }

  .filter-btn {
    max-width: 80%;
  }

  .filter-controller {
    display: block;
  }

  .button-filter-btn {
    padding: 0 12px;
  }
  .books-container {
    order: 1;
    margin: 0;
  }

  .books-shelf {
    grid-gap: 16px;
    grid-template-columns: repeat(auto-fit, minmax(50px, 150px));
    max-width: 100%;

    padding: 0 12px;

    justify-content: center;
  }

  .upload-parent-container {
    margin: 200px 0 0 20px;
    display: flex;
    flex-flow: column;
    align-items: flex-start;
  }

  .upload-section {
    margin-left: 30px;
  }

  .upload-container {
    max-width: 90%;
  }

  .upload-container button {
    width: 100%;
  }

  .heading-text {
    text-align: center;
    padding: 0 8px;
  }

  .heading,
  .sub-heading {
    width: 100%;
    text-align: center;
  }

  .sub-heading {
    padding: 0 10px;
  }

  .lawbook img {
    width: 100%;
    height: 70%;
    margin-bottom: 8px;
  }

  .books-shelf h1,
  .books-shelf h3 {
    text-align: center;
  }

  .ratings-container {
    width: 60%;
    margin: 0 auto;
  }

  .lawbook button {
    display: block;
    margin: 3% auto;
  }

  .search-field {
    max-width: 85%;
    padding-right: 10%;
    position: absolute;
    left: 0;
  }

  .search-manual {
    padding: 0 12px;
  }

  .sorry-text {
    text-align: center;
    margin-top: 15px;
  }

  /* .pagination-buttons {
    display: flex;
    justify-content: center;
    margin-top: 70px;
    margin-left: 10px;
    margin-right: 10px;
  } */
}
