

@media screen and (max-width: 768px) {
    .header-text, .background-text{
        padding: 0 12px;
        margin-left: auto;
        margin-right: auto;
    }
  .about-description {
    flex-flow: column;
    align-items: center;
  }

  .cover-container {
    margin: 0 auto;
  }

  .heading {
    margin: 0;
    position: relative;
    right: 110px;
  }

  .heading .review {
    padding: 0;
  }

  .similar {
    max-width: 80%;
  }

  .similar-lawbooks {
      margin: 0 auto;
  }
}

@media screen and (max-width: 444px) {
  .cover-container {
    max-width: 60%;
    flex-flow: column;
    align-items: center;
    margin: 0 auto;
  }

  .cover-container img {
    margin: 0;
  }

  .category {
    position: relative;
    right: 24px;
  }

  .title-text {
    font-size: 1.5rem;
    text-align: center;
  }

  .main-rating {
    text-align: center;
  }

  .caption-1,
  .caption-2 {
    text-align: center;
  }
  .heading {
    position: static;
    right: 0;
  }

  .cover-text {
    width: 90%;
  }

  .buttons {
    display: flex;
    justify-content: center;
  }

  .mark-icon {
    margin: 0 0 0 21%;
    width: 80%; 

      
  }

  
  .search-icon-text {
    width: 50%;
    top: 10px;
    
  }

  .first-bk h3 {
    max-width: 100%;
    padding-right: 30px;
  }
}

@media screen and (max-width: 289px) {
  .similar-lawbooks {
    justify-content: center;
  }

  .more-header {
    text-align: center;
  }

  .price-button {
    display: block;
    margin-left: 12%;
  }

  .similar-title-text {
    width: 80%;
    text-align: center;
  }

  .similar-author-text {
    padding-left: 5px;
  }

  .category {
    right: 0;
  }
}
