@media screen and (max-width: 580px) {
  .card {
    flex-flow: column;
    height: auto;
    max-width: 90%;
    align-items: start;
    margin: 0 5%;
    padding: 15px;

  }

  .card-content {
    margin: 0; 
    margin-top: 5px;
  }

  .card img {
    margin: 0;
   
  }

  .bookmark {
    right: 6%;
  }

  .button-container button:first-child {
    width: 80%;
    height: auto;
    padding: 4px;
  }

  .button-container button:last-child {
    width: 60%;
    height: auto;
    padding: 4px;
  }
}
