div.signup-options label {
  cursor: pointer;
}

.radio-label {
  transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;
}

.radio-button:checked + .radio-label {
  background-color: black;
  color: white;
  border-radius: 3px;
}

.radio-label {
  white-space: nowrap;
}

@media screen and (max-width: 580px) {
  form div.signup-options {
    max-width: 80%;
  }

  .bg-main-text {
    font-size: 2.5rem;
    padding: 0 5px;
  }

  .bg-text {
    margin: 0 auto;
    font-size: 0.85rem;
  }

  .bg-text,
  .input-container,
  .signup-btn {
    max-width: 80%;
  }

  .passwd-form {
    max-width: 100%;
  }

  .country-select-container {
    max-width: 80%;
    flex-direction: column;
    align-items: center;
    height: 120px;
    justify-content: space-between;
  }

  .country-select-container select {
    min-width: 100%;
  }

  .footer-text {
    text-align: center;
    margin-top: 10px;
    padding: 0 12px;
  }
}

@media screen and (max-width: 426px) {
  form div.signup-options {
    flex-flow: column;
    height: auto;
    width: 100%;
  }

  .radio-button + .radio-label {
    width: 100%;
    margin: 0 auto;
  }

  form div.signup-options label {
    margin: 6px 0px;
  }
  .other-options {
    max-width: 100%;
    flex-flow: column;
    align-items: center;
    height: 100px;
    justify-content: space-between;
  }

  .other-options button {
    margin: 0;
    width: 80%;
    font-size: 0.9rem;
    padding: 0 5px;
  }

  .footer-text {
    margin-left: 8px;
    margin-right: 8px;
  }
}

@media screen and (max-width: 163px) {
  .bg-main-text {
    font-size: 2rem;
    margin: 0;
    padding: 0 7px;
  }
}
