.hamburger-menu {
  cursor: pointer;
}
svg {
  transition: transform 500ms cubic-bezier(0.4, 0, 0.2, 1);
}
.active svg {
  transform: rotate(90deg);
}
path {
  transition: transform 500ms cubic-bezier(0.4, 0, 0.2, 1),
    stroke-dasharray 500ms cubic-bezier(0.4, 0, 0.2, 1),
    stroke-dashoffset 500ms cubic-bezier(0.4, 0, 0.2, 1);
}
path:nth-child(1) {
  transform-origin: 36% 40%;
}
path:nth-child(2) {
  stroke-dasharray: 29 299;
}
path:nth-child(3) {
  transform-origin: 35% 63%;
}
path:nth-child(4) {
  stroke-dasharray: 29 299;
}
path:nth-child(5) {
  transform-origin: 61% 52%;
}
path:nth-child(6) {
  transform-origin: 62% 52%;
}
.active path:nth-child(1) {
  transform: translateX(9px) translateY(1px) rotate(45deg);
}
.active path:nth-child(2) {
  stroke-dasharray: 225 299;
  stroke-dashoffset: -72px;
}
.active path:nth-child(3) {
  transform: translateX(9px) translateY(1px) rotate(-45deg);
}
.active path:nth-child(4) {
  stroke-dasharray: 225 299;
  stroke-dashoffset: -72px;
}
.active path:nth-child(5) {
  transform: translateX(9px) translateY(1px) rotate(-45deg);
}
.active path:nth-child(6) {
  transform: translateX(9px) translateY(1px) rotate(45deg);
}

.sub-menu {
  max-width: 100px;
  position: relative;
  margin-top: 15px;
  right: 37px;
  box-sizing: border-box;
  top: 6px;
  z-index: 9999;
}

.sub-menu li {
  z-index: 999;
  position: relative;
}
.sub-menu::before {
  content: "";
  height: 20px;
  width: 20px;
  border-top: 10px solid rgba(229, 231, 235);
  border-left: 10px solid rgba(229, 231, 235);
  position: absolute;
  margin-top: -6px;
  transform: rotate(45deg);
  left: 37%;
}

.bell-icon {
  height: 0;
  display: block;
}

.main-content {
  position: relative;
  margin-top: 15px;
  right: -6px;
  box-sizing: border-box;
  top: 40px;
}

.main-content::before {
  content: "";
  height: 20px;
  width: 20px;
  border-top: 10px solid rgba(243, 244, 246);
  border-left: 10px solid rgba(243, 244, 246);
  position: absolute;
  margin-top: -8px;
  transform: rotate(45deg);
  right: 20%;
}

@media screen and (max-width: 1285px) {
  /* .main-nav-container {
  } */

  .hamburger-menu {
    display: block;
  }

  .nav-container {
    flex-flow: column;
    position: absolute;
    transition: transform 0.5s ease-in-out;
    transform: translateX(-100%);
    align-items: flex-start;
    height: 600px;
    justify-content: space-evenly;
    background-color: black;
    top: 0;
    z-index: 30;
    max-width: 100%;
  }

  .nav-container img.desktop {
    display: none;
  }

  img.mobile {
    display: block;
    position: relative;
    top: -35px;
    left: -25px;
  }

  .nav-items {
    flex-flow: column;
    height: 100%;
    justify-content: space-evenly;
    margin-left: 40px;
    z-index: 40;
  }

  .bell-icon {
    height: 8px;
    width: 8px;
  }

  ul.nav-items li {
    margin: 0;
    display: inline-block;
  }

  .move {
    transform: translateX(0);
  }

  .main-content {
    position: relative;
    margin-top: 15px;
    left: 190px;
    box-sizing: border-box;
    top: -50px;
    display: inline-block;
    max-width: 70%;
  }

  .main-content::before {
    content: "";
    height: 20px;
    width: 20px;
    border-left: 10px solid rgba(243, 244, 246);
    border-bottom: 10px solid rgba(243, 244, 246);
    position: absolute;
    margin-left: -150px;
    top: 40px;
    transform: rotate(45deg);
    left: -2px;
    display: inline;
  }

  .sub-menu {
    position: relative;
    margin-top: 15px;
    right: 35px;
    box-sizing: border-box;
    top: 1px;
  }

  .sub-menu li {
    padding-top: 3px;
    padding-bottom: 3px;
  }
  .sub-menu::before {
    content: "";
    height: 20px;
    width: 20px;
    border-top: 10px solid rgba(229, 231, 235);
    border-left: 10px solid rgba(229, 231, 235);
    position: absolute;
    margin-top: -6px;
    transform: rotate(45deg);
    left: 37%;
  }

  li.sign-up {
    margin: 0;
    text-align: left;
  }
}

@media screen and (max-width: 405px) {
  .bell-icon {
    height: 20px;
  }
  .notification-list {
    max-width: 200px;
    position: relative;
  }

  .main-content {
    top: 10px;
    width: 0;
    left: 130px;
  }

  .main-content::before {
    content: "";
    top: 4px;
    left: 12px;
  }
}
