    padding: 0 8px;
    flex-wrap: wrap;
    align-items: stretch;
}
h1.main-heading{
    font-size: 1.4rem;
    padding-left: 10px;
    padding-right: 10px;
}
.container div{
    margin: 10px 8px;
}
.container div span{
    width: 100%;
}
}