div.Q-A{
    order: 1;
}

div.filter-tags{
order: 2;
}

@media screen and (max-width: 800px) {
  .content {
    flex-flow: column;
    align-items: center;

  }

  .content{

    margin-right: auto;
    margin-left: auto;
  }

  .filter-tags {

   display:none;
  }

   

  .filter-btn{
    display: block;
  }

  div.Q-A{
      order: 2;
      
  }

 
  
  
}

@media screen and (max-width: 560px) {

  .mobile-filter-btn-container{
   max-width: 70%;
  }
  div.Q-A {
    max-width: 100%;
    
  }

  div.answer1-container{
    max-width: 90%;
    margin-left: auto;
    margin-right: auto;

  }


  div.answer1-content img{
    max-width: 40px;
    max-height: 40px;
}



  div.question-options {
    max-width: 80%;
    height: auto;
    flex-flow: column;
    align-items: center;
    
  }

  div.question-options label {
    width: 100%;
  }

  .question-section,
  .select-container,
  .caption-text {
    max-width: 90%;
    margin-left: auto;
    margin-right: auto;
  }

  .select-container {
    flex-flow: column;
    align-items: center;
  }
  .select-container select {
    margin: 3px 0px;
  }

  .filter-tags .filter {
    max-width: 90%;
  }

  .popular-tags {
    max-width: 95%;
  }

  .popular-tags button {
    width: 90%;
  }

  .more-container{
    margin: 0 15px;
  }

  .update-box{
    position: relative;
  }


}

@media screen and (max-width: 450px){
  .answer1-header .question-details{
    width: 60%;

  }

  .icons-container{
    flex-flow: column;
    justify-content: center;
    
  }

  .icons-container .icons{
    margin: 0 auto;
  }

  .comment-btn{
    margin: 0 auto;
    width: 97%;
    
    
  }


 
}
