.image-container {
  max-width: 1300px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(230px, 1fr));
}

.pagination-buttons {
  margin: 40px 5px;
  display: flex;
  max-width: 600px;
  justify-content: center;
  padding: 5px;

}
.pagination-buttons a {
  margin: 12px 5px;
  border: 1px solid grey;
  border-radius: 5px;
  padding: 6px;
  font-size: 1rem;

}

.pagination-buttons a:hover {
  color: white;
  background-color: black;
}

.pagination-active a {
  color: white;
  background-color: black;
}



@media screen and (max-width: 588px) {
  .image-container {
    justify-items: center;
  }
}
