.big-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  width: 100%;
}

.main-container {
  max-width: 100%;
  margin-top: auto;
}

@media screen and (max-width: 480px) {
  .container {
    max-width: 80%;
  }

  .container-child-1 {
    width: 100%;
    height: 200px;
    flex-flow: column;
    justify-content: space-evenly;
    margin: 0;
    margin-left: 8px;
  }

  .container-child-1 li {
    width: 50%;
    text-align: left;
  }

  .container-child-2 {
    width: 100%;
    height: 110px;
    flex-flow: column;
    align-items: flex-start;
    justify-content: space-between;
    margin: 8px 0;
    margin-left: 8px;
  }

  .socials-btn {
    font-size: 1.2rem;
    width: 80px;
    margin: 0;
  }

  .footer-text {
    text-align: left;
    display: inline-block;
  }
}
