.all-categories-container{
    margin: 0 auto;
    
}



.all-categories-sub-container{
    display: grid;
    grid-template-columns: repeat(auto-fit, 150px);
    grid-gap: 14px;
    justify-items: center;
    margin: 0 auto;
       
}



/* .all-categories-container h2{
    
} */





@media screen and (max-width:700px){

   
    .all-categories-container{
        max-width: 70%;
        
       
    }
    
    .all-categories-sub-container{
        max-width: 100%;
    }

    form{
        margin: 0 25px;
    }

    .background-text{
        font-size: 2rem;
    }

    .background-subtext{
        padding: 0 20px;
    }
}


@media screen and (max-width:210px){
    .background-text{
        font-size: 1rem;
    }

    .background-subtext{
        font-size: .8rem;
    }
}



@media screen and (max-width:448px){

    .all-categories-sub-container{
        justify-content: center;
        margin-top: 50px;
    }

    .all-categories-container .big-letter{
        position: relative;
        top: 35px;
        left: 30px;
        
        margin-top: 10px;
        
        
    }
}