@media screen and (max-width: 680px) {
  h1.main-text {
    font-size: 2.5rem;
    margin: 0 8px;
  }

  .notice-cards {
    max-width: 90%;
}

.card-content{
    max-width: 100%;
}
.icon-text{
    left: -40px;
}
}
@media screen and (max-width: 320px) {
  h1.main-text {
    font-size: 1.5rem;
    margin: 0 auto;
  }
.small-text{
    font-size: .8rem;
    padding: 0 8px;
}
}
