html {
  scroll-behavior: smooth;
}

option {
  color: black;
}

.darkmode-toggle {
  z-index: 999;
}

@media screen and (max-width: 850px) {
  .switch-container {
    margin: 30px 10px;
  }
}

@media screen and (max-width: 660px) {
  .options {
    width: 60%;
    height: auto;
  }
  .options form {
    width: 100%;
    flex-flow: column;
    padding: 4px 5px;
    margin: 0 auto;
  }

  .question-search {
    max-width: 90%;
    display: flex;
    height: 60px;
    margin-bottom: 40px;
    flex-flow: column;
  }
  .question-search input {
    min-width: 96%;
    margin: 0 auto;
    margin-bottom: 50px;
  }
  .question-search button {
    min-width: 100%;
    margin: 0 auto;
    padding: 6px 0;
  }

  .options form select {
    width: 100%;
    margin: 3px auto;
  }
  .switch-container {
    margin: 0 10px;
    position: relative;
    top: -18px;
  }
  .switch-container button {
    font-size: 1rem;
    height: 20%;
  }
  .search-btn {
    width: 100%;
    height: 30px;
  }

  .sign-btn {
    height: auto;
    padding: 4px 0;
  }

  .second-text {
    font-size: 1.6rem;
    padding: 0 13px;
  }
}

@media screen and (max-width: 329px) {
  .main-header h1 {
    font-size: 2.5rem;
  }
}
