html {
  scroll-behavior: smooth;
}

.similar-lawyers-layout {
  width: 1100px;
  display: grid;
  grid-template-columns: repeat(2, 530px);
  justify-content: center;
  justify-items: center;
  margin: 0 auto;
  grid-row-gap: 15px;
  grid-column-gap: 12px;
}

ul.sub-navbar li {
  margin: 0 10px;
}

@media screen and (max-width: 1126px) {
  .similar-lawyers-layout {
    max-width: 100%;
    grid-template-columns: repeat(auto-fit, minmax(50%, 100%));
  }
}

@media screen and (max-width: 790px) {
  .nava-container {
    min-height: 650px;
  }
  .about-section,
  .practice-section,
  .qualification-section,
  .contact-section,
  .reviews-section {
    max-width: 100%;
    padding: 0 5%;
  }

  .qualification-section-table-layout table {
    width: 100%;
  }

  .qualification-section-table-layout h1 {
    margin-left: 12px;
  }

  .qualification-section-table-layout table td,
  .qualification-section-table-layout table th,
  .qualification-section-table-layout table tr {
    padding: 2px 12px;
    width: 44%;
  }
}

@media screen and (max-width: 570px) {
  ul.sub-navbar{
    flex-flow: column;
    align-items: center;
  }

  ul.sub-navbar li{
    margin: 5px 0px;
  }

  .header-footer {
    flex-flow: column;
    align-items: center;
  }

  .header-footer button {
    position: static;
    right: 0;
  }

  .header-footer .ratings {
    margin: 0 0 5px 0;
    align-self: center;
    left: 0;
  }

  .bk-btn {
    align-self: flex;
    margin: 0 5px 0 0;
  }

  .bookmark {
    align-self: flex-end;
    padding: 0;
  }

  .similar-lawyers-layout {
    justify-items: normal;
  }

  .qualification-section-table-layout table td,
  .qualification-section-table-layout table th,
  .qualification-section-table-layout table tr {
    width: 40%;
  }
}

@media screen and (max-width: 444px) {
  .practice-section .buttons-container {
    flex-flow: column;
    align-items: center;
    padding-left: 10px;
    padding-right: 10px;
  }

  .buttons-container button {
    width: 80%;
  }

  h3 {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
  }
  /* .qualification-section-table-layout{
    max-width: 100%;
  }

  .qualification-section-table-layout table{
    max-width: 100%;
  } */
}

@media screen and (max-width: 310px) {
  .qualification-section-table-layout table tr {
    display: flex;
    flex-flow: column;
    width: 100%;
    align-items: center;
  }
  .qualification-section-table-layout h1 {
    text-align: center;
  }
  .header-footer {
    max-height: 100%;
  }

  .header-footer button {
    width: 60%;
  }
}
