@media screen and (max-width: 640px) {
  .about-content {
    flex-flow: column;
    align-items: center;
    max-width: 100%;
  }

  .container-content{
      max-width: 100%;
      padding: 20px;
      
      margin: 0 auto;
  }

  .container-content{
      min-width: 100%;
  }
  

  .about-content img, .mission-container img{
      width: 500px;
      padding: 0 20px;
      
  }

  
.mission-container{
    max-width: 100%;
    
}

.mission-text, .mission-text h1{ 
    max-width: 90%;
    padding: 0 8px;
}


  
}
